@use 'base/variables' as v;
@use 'base/mixins' as m;

.tabs {

    @include m.tablet {
        display: flex;
        gap: 1rem;   
    }

    button {
        cursor: pointer;
        background-color: v.$azul;
        border: none;
        color: v.$blanco;
        padding: 1rem;
        display: block;
        width: 100%;
        margin-bottom: 1rem;
        text-transform: uppercase;
        font-weight: v.$bold;
        transition: background-color 0.4s ease;

        &:hover {
            background-color: v.$azulOscuro;
        }
        &.actual {
            background-color: v.$blanco;
            color: v.$azul;
        }
    }
}