@use "base/variables" as v;
@use "base/mixins" as m;

.formulario {
    margin-top: v.$separacion;
    width: 100%;
}

.campo {
    display: flex;
    margin-bottom: 2rem;
    align-items: center;

    label {
        flex: 0 0 10rem;
    }

    input {
        flex: 1;
        border: 0.3rem solid v.$grisOscuro;
        padding: calc(v.$separacion / 4);
        border-radius: 1rem;
        transition: all 0.4s ease-out 0s;

        &:hover {
            border: 0.3rem solid v.$azul;
            transition: all 0.2s ease-in 0s;
        }

        &:disabled {
            background-color: lighten(v.$grisOscuro, 30%);
            color: v.$blanco;
            cursor: not-allowed;
        }
    }
}


.password {
    display: inline;
    visibility: visible;
    cursor: pointer;
    position: absolute;
    right: 5.2rem;
    user-select: none;
    color: v.$negro;
    transition: all 0.4s ease-out 0s;

    &:hover {
        transition: all 0.2s ease 0s;
        color: v.$azul;
    }

}

.negro {
    color: v.$negro;
}

.azul {
    color: v.$azul;
}