@use "base/variables" as v;
@use "base/mixins" as m;

.acciones {
    margin-top: v.$separacion;
    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    
    a {
        display: block;
        color: v.$blanco;
        margin-bottom: calc(v.$separacion / 2);
        text-align: center;
    }
}

.hover {
    transition: all 0.4s ease-out 0s;

    &:hover {
        color: v.$azul;
        transition: all 0.2s ease-in 0s;
    }
}