@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-app {
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }

    .imagen {
        height: 20rem;
        background-image: url(../img/1.jpg);
        background-size: cover;
        background-position: center center;
        @include m.tablet {
            height: auto;
        }
    }
    .app {
        width: 100%;
        margin: 0 auto;
        padding: 3rem;
        padding-right: 2rem;
        @include m.tablet {
            overflow: scroll;
            overflow-x: hidden;
        }

        &::-webkit-scrollbar {
            width: .8rem;
        }
          
        &::-webkit-scrollbar-thumb {
            background-color: v.$negro;
            border: 0.1rem solid v.$blanco; 
            border-radius: 20px;       
        }
    }
}

.barra {
    padding: 2rem;
    border-bottom: .1rem solid v.$azul;
    margin: v.$separacion 0;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .boton {
        @include m.boton(v.$rojo, v.$blanco);
        margin: 0;

        &:hover {
            background-color: v.$rojoOscuro;
        }
    }

    span {
        color: v.$azul;
    }
}

.barra-servicios {
    margin-bottom: v.$separacion;
    @include m.tablet {
        display: flex;
        justify-content: space-between;
    }

    .boton {
        margin: 0 0 2rem 0;
    }
}