@use "base/variables" as v;

.citas {
    list-style: none;
    padding: 0;

    li {
        padding: 1rem;
        border-top: 1px solid v.$blanco;

        p {
            font-weight: v.$bold;
            color: v.$azul;
        }

        span,
        .servicio {
            font-weight: v.$regular;
            color: v.$blanco;
        }

        &:hover {
            border-top: 1px solid v.$azul;
        }
    }
}
.precio {
    color: v.$azul;
}

.total {
    color: v.$azul;
    font-weight: v.$bold;
    span {
        font-weight: v.$regular;

        &:hover {
            font-weight: v.$bold;
        }
    }
}

.servicios {
    @extend .citas;

    .acciones {
        margin: 0;
    }
    .boton,
    .boton-eliminar {
        margin: 0;
    }
}